/* 기존 CSS 클래스 */
.button-container {
  display: flex;
  justify-content: center;
  gap: 5px; /* 버튼 간 간격을 5px로 줄임 */
  flex-wrap: wrap;
}

/* 새로운 CSS 클래스 */
.main-button-container {
  display: flex;
  justify-content: center;
  gap: 70px; /* 원래 간격 유지 */
  margin-bottom: 10px;
}

/* 나머지 CSS는 기존과 동일합니다. */
.body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #333;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.navbar-left {
  display: flex;
  align-items: center;
}

.navbar-logo {
  font-size: 24px;
  margin-right: 20px;
}

.navbar-title {
  font-size: 20px;
}

.navbar-right {
  display: flex;
  align-items: center;
}

.navbar-link {
  margin-left: 20px;
  color: white;
  text-decoration: none;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: black;
  height: 100vh;
  color: white;
}

.intro-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: opacity 1s ease-out;
  transform: translateY(-50px);
}

.logo {
  width: 150px;
  margin-bottom: 5px;
  margin-top: 20px;
}

.appname {
  color: white;
  text-align: center;
  font-size: 50px;
  margin-bottom: 30px;
  font-weight: bold;
}

.main-button {
  width: 300px;
  height: 50px;
}

.bottom-space {
  height: 50px;
  background-color: black;
}

.about-section {
  background-color: white;
  padding: 100px 20px;
  color: black;
  opacity: 0;
  transform: translateY(100px);
  transition: opacity 1s ease-out, transform 1s ease-out;
  padding-bottom: 240px;
  padding-top: 220px;
}

.about-section.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.about-content {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 100px;
}

.about-title {
  font-size: 48px; 
  font-weight: bold;
  flex: 1;
  padding-top: 80px;
}

.about-description {
  flex: 2;
  font-size: 18px;
  line-height: 1.6;
  margin-left: 20px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 홈 페이지에서만 사용할 애니메이션 클래스 */
.home .logo, .home .appname, .home .button-container, .home .main-button-container {
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 1s ease-in-out, transform 0.5s ease-in-out;
}

.home .logo.visible, .home .appname.visible {
  opacity: 1;
  transform: translateY(0);
}

.home .main-button-container.visible {
  opacity: 1;
  transform: translateY(0);
}
